import service from '@/utils/request'

// 出版社列表
export const getpubList = (data) => {
    return service({
      url: '/getpubList',
      method: 'get'
    })
}

// 出版社列表
export const getDiscountList = (data) => {
    return service({
      url: '/index',
      method: 'get',
      data: data
    })
}

// 出版社列表
export const getDiscountDetail = (data) => {
    return service({
      url: '/discountDetail',
      method: 'get',
      data: data
    })
}