<template>
  <div id="app">
    <h1 class="d-flex justify-content-center my-4 fs-2 text-primary">云间世纪销售排行</h1>
    <search @getLoadEcharts="getLoadEcharts" ref="searchRef"></search>
    <!-- <container></container> -->
    <div>
      <div class="d-flex justify-content-center flex-wrap">
        <div :class="!detailShow ? 'cc-max-min' : 'hw300'">
          <div id="echart"></div>
        </div>
        <div v-if="detailShow" class="hw400">
          <table class="table tb-fs12">
            <thead>
              <tr>
                <th scope="col">折扣内排行</th>
                <th scope="col">博库分销折扣</th>
                <th scope="col">书名</th>
                <th scope="col">云间销售占比</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in tableData" :key="index">
                <th scope="row">{{ index+ 1 }}</th>
                <td>{{ item.discount }}</td>
                <td>{{ item.book_name }}</td>
                <td>{{ (item.shiyang / saleNumObj.shiyangSum * 100).toFixed(6) }} %</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div v-if="detailShow" class="d-flex justify-content-center ">
        <div class="row-chart-st">
          <div id="rowChart"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import search from '@/components/search.vue'
import { getDiscountDetail } from '@/api/init'
// import Container from './components/container.vue'
export default {
  components: {
    search,
    // Container
  },
  data() {
    return {
      detailShow: false,
      tableData: [],
      saleNumObj: {
        shiyangSum: 1
      },
      labelData1: [],
      labelData2: []
    };
  },
  mounted() {
    // this.getLoadEcharts();
  },
  methods: {
    changeSize(myChart) {
      return () => myChart.resize()
    },
    changeSize2(myChart) {
      return () => myChart.resize()
    },
    getLoadEcharts(childObj) {
      console.log(childObj);
      const cData = childObj.data.map(item => ({
        name: item.discount,
        value: item.pzNum
      }))
      const _this = this
      const myChart = this.$echarts.init(
        document.getElementById("echart")
      );
      window.addEventListener('resize', this.changeSize(myChart))
      myChart.on('click', function (params) {
        console.log(params.data.name);
        _this.$nextTick(() => {
          // _this.changeSize(myChart)()
          _this.getLoadEcharts2(params.data.name, myChart)
        })
      });
      const option = {
        title: {
          text: childObj.name || '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        // legend: {
        //   orient: 'vertical',
        //   left: 'left'
        // },
        series: [
          {
            name: '销售排行',
            type: 'pie',
            radius: ['40%', '70%'],
            itemStyle: {
              borderRadius: 10,
              borderColor: '#fff',
              borderWidth: 2
            },
            label: {
              show: true,
              position: 'inner'
            },
            data: cData || [],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    },
    getLoadEcharts2(discount, myChart) {
      const params = {
        startTime: this.$refs.searchRef.dateRange[0],
        endTime: this.$refs.searchRef.dateRange[1],
        publisher: this.$refs.searchRef.value,
        discount
      }
      getDiscountDetail(params).then(res => {
        console.log(res);
        this.detailShow = true
        if (res.code === 200) {
          this.tableData = res.data.data1
          console.log(this.tableData);
          this.saleNumObj = res.data.data2[0]
          this.labelData1 = this.tableData.map(item => {
            return item.book_name + ', ' + (item.shiyang / this.saleNumObj.shiyangSum * 100).toFixed(6) + '%'
          })
          this.labelData2 = this.tableData.map(item => {
            return item.shiyang
          })
          this.$nextTick(() => {
            this.rewriteChart2()
            this.changeSize(myChart)()
          })
        } else {

        }
      })
    },
    rewriteChart2() {
      const myChart = this.$echarts.init(
        document.getElementById("rowChart")
      );
      window.addEventListener('resize', this.changeSize2(myChart))
      const option = {
        xAxis: {
          axisLabel: {
            color: '#999'
          },
          position: 'top',
          type: 'value',
        },
        yAxis: {
          data: this.labelData1,
          axisLabel: {
            inside: true,
            color: '#000',
          },
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          },
          z: 10,
          type: 'category'
        },
        series: [
          {
            data: this.labelData2,
            type: 'bar',
            itemStyle: {
              color: '#f7db73'
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.changeSize())
    window.removeEventListener('resize', this.changeSize2())
  }
}
</script>
