<template>
    <div class="w-100">
        <div class="d-md-block d-none">
            <div class="d-flex justify-content-center w-100">
                <el-date-picker v-model="dateRange" type="daterange" range-separator="-" value-format='yyyy-MM-dd' start-placeholder="开始日期"
                    end-placeholder="结束日期" size="mini" unlink-panels :append-to-body="false">
                </el-date-picker>
                <el-select v-model="value" placeholder="出版社" size="mini" class="mx-2" clearable>
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"
                        size="mini">
                    </el-option>
                </el-select>
                <el-button type="primary" size="mini" @click="getData">搜索</el-button>
            </div>
        </div>
        <div class="d-md-none">
            <div class="d-flex justify-content-center w-100 my-3">
                <el-date-picker v-model="dateRange" type="daterange" range-separator="-" value-format='yyyy-MM-dd' start-placeholder="开始日期"
                    end-placeholder="结束日期" size="mini" unlink-panels :append-to-body="false" :editable="false">
                </el-date-picker>
            </div>
            <div class="d-flex justify-content-center w-100">
                <el-select v-model="value" placeholder="出版社" size="mini" class="me-2" clearable>
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"
                        size="mini">
                    </el-option>
                </el-select>
                <el-button type="primary" size="mini" @click="getData">搜索</el-button>
            </div>
        </div>
    </div>
</template>

<script>
import { getpubList, getDiscountList } from '@/api/init'
export default {
    data() {
        return {
            dateRange: [],
            options: [],
            value: ''
        }
    },
    mounted() {
        this.getPublishList()
    },
    methods: {
        getData() {
            if(!this.dateRange.length) return this.$message.error('请选择开始或结束时间')
            if(!this.value) return this.$message.error('请选择出版社')
            const params = {
                startTime: this.dateRange[0],
                endTime: this.dateRange[1],
                publisher: this.value
            }
            getDiscountList(params).then(res => {
                if(res.code === 200 && res.data.length) {
                    this.$emit('getLoadEcharts', {data:res.data, name: this.value, date: this.dateRange})
                } else {
                    this.$emit('getLoadEcharts', {data:res.data, name: this.value, date: this.dateRange})
                }
            })
        },
        getPublishList() {
            getpubList().then(res => {
                if(res.code === 200 && res.data.length) {
                    this.options = res.data.map(item => ({
                        label: item.publisher_name,
                        value: item.publisher_name,
                    }))
                } 
            })
        },
    }
};
</script>

